import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSquare,faBed, faToilet, faVectorSquare,faCar } from '@fortawesome/free-solid-svg-icons';


function DetailAmenities({property}) {

    
    return (
        <React.Fragment>
            <div className="">
                <h2>Propiedad en {property[0].mode} ID #{property[0].id}</h2>
            </div>
           
            <div className="info">
                <div>
                    <h2>{property[0].money} {property[0].price}</h2>
                    <p>{property[0].address}</p>
                    <p>{property[0].location}-{property[0].departament}-{property[0].province}</p>
                </div>
                <div className='amenities'>
                    <FontAwesomeIcon icon={faBed} />
                    <p>Dormitorios: {property[0].bedroom}</p>
                    <FontAwesomeIcon icon={faToilet} />
                    <p>Baños: {property[0].bathroom}</p>
                </div>
                <div className='amenities'>
                    <FontAwesomeIcon icon={faVectorSquare} />
                    <p>Sup Total: {property[0].m2Total} m2</p>
                    <FontAwesomeIcon icon={faSquare} />
                    <p>Sup Cub.: {property[0].m2} m2</p>
                    {(property[0].garage) &&
                        <>
                            <FontAwesomeIcon icon={faCar} />
                            <p>Cochera</p>
                        </>}
                </div>

            </div>
        </React.Fragment>
    )
}



export default DetailAmenities;