import React from 'react';


import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faDog, faFileContract, faDoorOpen, faWaterLadder,faFileInvoiceDollar, 
    faDollarSign, faHouseLock,faHourglassStart, faHouseCircleCheck,faBuilding,
    faTemperatureArrowUp, faCouch } from '@fortawesome/free-solid-svg-icons';



function DetailServices(property) {
    console.log(property);
    return (
        <React.Fragment>
            <h4 >Servicios que ofrece</h4>
            <div className='containerServices'>

               

                    <div className='services'>
                        <FontAwesomeIcon icon={faDog} />
                        <p>Mascotas: {property.property[0].pets ? 'Si' : 'No'}</p>

                        <FontAwesomeIcon icon={faFileContract} />
                        <p>Apto Crédito Hipetacario: {property.property[0].credit ? 'Si' : 'No'}</p>


                        <FontAwesomeIcon icon={faDoorOpen} />
                        <p>Cant. ambientes: {property.property[0].qtyScope}</p>

                        <FontAwesomeIcon icon={faWaterLadder} />
                        <p>Piscina: {property.property[0].pool ? 'Si' : 'No'} </p>

                    </div>
                    <div className='services'>
                        <FontAwesomeIcon icon={faFileInvoiceDollar} />
                        <p>Expensas: {property.property[0].expenses ? 'Si' : 'No'} </p>
                        
                        <FontAwesomeIcon icon={faDollarSign} />
                        <p>Valor Expensas: $ {property.property[0].expensesCost} </p>
                        
                        <FontAwesomeIcon icon={faHouseLock} />      
                        <p>Barrio Privado: {property.property[0].private ? 'Si' : 'No'}</p>

                        <FontAwesomeIcon icon={faHourglassStart} />
                        <p>Antigüedad: {property.property[0].antiquity}</p>
                    </div>
                    
                    <div className='services'>
                        <FontAwesomeIcon icon={faHouseCircleCheck} />
                        <p>Estado de Consevación: {property.property[0].careful}</p>

                        <FontAwesomeIcon icon={faBuilding} />
                        <p>Plantas: {property.property[0].floors} </p>

                        <FontAwesomeIcon icon={faTemperatureArrowUp} />
                        <p>Calefacción Central: {property.property[0].heat? 'Si' : 'No'} </p>
                        
                        <FontAwesomeIcon icon={faCouch} />
                        <p>Amoblado: {property.property[0].furniture? 'Si' : 'No'} </p>
                    </div>

                
            </div>
        </React.Fragment >
    )
}



export default DetailServices;